<template>
    <div class="contribute">
      <Header class="headerMenu"></Header>
      <div class="wraper">
        <form v-on:submit.prevent="postPost" enctype="multipart/form-data" ref="formPost">
          <div class="firstPart">
            <div class="head">
              <h2>This platform needs you !</h2>
            </div>
            <div class="content">
              <div class="communityPart">
                <div class="partContent">
                  <h3 class="community">Your story matter and it can help others !</h3>
                  <div class="button">
                    <label>
                      <input name="MainType" type="radio" value="community" v-model="MainType" @change="part2">contribute to the Community
                    </label>
                  </div>

                </div>
              </div>
              <div class="archivesPart">
                <div class="partContent">
                  <h3 class="archives">What have you seen/read/drunk lately that you want to share</h3>
                  <div class="button">
                    <label>
                      <input name="MainType" type="radio" value="archives" v-model="MainType" @change="part2">contribute to the  Archive
                    </label>
                  </div>

                </div>
              </div>
              <div class="notice">
                Of course we will review contributions before they go live. <br> Thank you so much
              </div>
            </div>

          </div>



          <div class="communitySecondPart" v-if="isCommunity">
            <div class="headPart">
              <h2>
                Contribute to the community
              </h2>
              <div class="imageBack">

              </div>
            </div>
            <div class="description">
              Each story is different and unique and we want to hear about yours.
              We want to know how medication has helped you, or how you didn’t like it. How did you start taking them, and stop taking them? Well all those kinds of things!
              Of course you only get to share what you feel comfortable sharing.
            </div>

            <div class="videoBooth subtitle">
              <div class="imageBackground">

              </div>
              <div class="content">
                Do an video interview. It’s super easy!
              </div>
              <div class="action">
                <div> <a href="http://m.me/poppillsproject" target='blank'>go to videobooth (messenger)</a></div>
              </div>
            </div>
            <div class="picture subtitle">
              <div class="imageBackground">
              </div>
              <div class="content">
                Share a picture and/or some thoughts.
              </div>
              <div class="action" @click="goToForm">
                Go to pictures and thoughts
              </div>
            </div>
          </div>


          <div class="secondPart">

            <div class="secondPart__headPartCommunity" v-if='isCommunity'>
              <div class="headPart">
                <h2>
                  Pictures and thoughts
                </h2>
                <div class="imageBack">

                </div>
              </div>
              <div class="description">
                Your experience with behavior medication matters and there are many ways to translate it. Feel free to contribute pictures that you’ve made which express your feelings about meds. They can be of anything –literally- as long as they are truthful! Pictures of yourself with/without medication, pictures of your meds, your room, your friends, you bike, your family, a butterfly, the doll that gives you comfort, etc…
                You feel more comfortable with words? That’s fine too! Drop us a note, we’d love to read about your experience, as well as what words of wisdom you may have for others.
              </div>

              <div class="subtitle">
                Import and share
                pictures and/or thoughts
              </div>
            </div>
            <div class="secondPart__headPartArchives" v-if='isArchives'>
              <div class="headPart">
                <h2>
                  Contribute to the archives
                </h2>
                <div class="imageBack">

                </div>
              </div>
              <div class="description">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation.
              </div>
              <div class="subtitle">
                Past link or upload artwork from your phone
              </div>
            </div>

            <div class="formSecond">
              <input class="title_input" v-model='title' type="text" placeholder="Title">

              <div class="images">
                <img class="image-preview" src="" ref="image_preview"/>
                <label for="fileUpload"> Add some file</label>
                <input
                  id="fileUpload"
                  @change="pictureChange"
                  ref="workshop_picture"
                  type="file" name="image" value="">

              </div>
              <div class="addContent">
                <input type="text" placeholder="Link to an additionnal content" v-model="additionnal_content">
              </div>
              <textarea placeholder="You can write some thoughts here …" v-model='content'> </textarea>


              <h2 class="subtitle category">Select the category of
                the artwork</h2>
              <div class="categories">
                <div class="categories__item" v-for="category in categoryArchives">
                  <input  :id="category.id" type="checkbox" :name="category.id" :value="category.id" v-model="categoryFilter"><label :for="category.id">{{ category.name }}</label>
                </div>
              </div>

              <input type="submit" value="I'M DONE">
            </div>

          </div>
          <div class="form-validate">
            <h1>
              This is just great !
            </h1>
            <div class="subtitle">
              Thank you so much for taking this time. Your story can be very helpful to the Community. It matters !
            </div>
            <router-link class="link" to="/">
              <div class="button"><p>Back to the homepage</p></div>
            </router-link>
          </div>
        </form>

      </div>

    </div>
</template>

<script>
    import axios from 'axios'
    import config from '../config'
    import TweenMax from 'gsap'
    import Header from './Header'

    export default{
      components: {
        Header
      },
      data () {
        return {
          MainType: '',
          isCommunity: false,
          isArchives: false,
          title: '',
          content: '',
          imagePost: '',
          postId: '',
          imageId: '',
          categories: '',
          categoryArchives: [],
          categoryFilter: [],
          additionnal_content: ''
        }
      },
      mounted: function () {
        this.$wraper = document.querySelector('.wraper')

        this.$http.get(`${config.dataUrl}/categories?per_page=20`).then(response => {
          // get body data
          this.categories = response.body
          this.organizeCategories()
        }, response => {
          console.log(response)
          // error callback
        })
      },
      methods: {
        postPost: function () {
          console.log('fuckThat')
          var that = this
          const request = axios.create({
            baseURL: `${config.dataUrl}`,
            headers: {
              'Authorization': 'Basic cm9vdDowNSpAcndeJTVPckckT09sVG8='
            }
          })
          request.post('posts', {
          // request body
            title: this.title,
            content: this.content,
            categories: this.categoryFilter,
            additionnal_content: this.additionnal_content
          }).then(function (response) {
            that.postId = response.data.id
          }).catch(function (error) {
            console.log(error)
          })
          TweenMax.to('.wraper', 0.5, {left: '-200%'})
          this.scrollTop()
          // MEDIA
          const requestMedia = axios.create({
            baseURL: `${config.dataUrl}`,
            headers: {
              'Authorization': 'Basic cm9vdDowNSpAcndeJTVPckckT09sVG8=',
              'Content-Type': 'multipart/form-data'
            }
          })
          console.log('pictureChange', this.pictureChange)
          requestMedia.post('media',
            this.imagePost
          ).then(function (response) {
            console.log(response.data.id)
            that.imageId = response.data.id
            that.asignImage()
          }).catch(function (error) {
            console.log(error)
          })
        },
        asignImage: function () {
          var that = this
          var url = 'posts/' + that.postId
          console.log('url', url)
          const requestLogMedia = axios.create({
            baseURL: `${config.dataUrl}`,
            headers: {
              'Authorization': 'Basic cm9vdDowNSpAcndeJTVPckckT09sVG8='
            }
          })
          requestLogMedia.put(url, {
          // request body
            featured_media: that.imageId
          }).then(function (response) {
            console.log(response)
          }).catch(function (error) {
            console.log(error)
          })
        },
        pictureChange (e) {
          console.log(e)
          var files = this.$refs.workshop_picture.files
          var uploadedFile = files[0]
          var fileExtension = uploadedFile.name.split('.').pop()
          if ((/(gif|jpg|jpeg|png)$/i).test(fileExtension)) {
            console.log(uploadedFile)
            // this.errors.remove('image')
            this.showImagePreview(uploadedFile)
            var data = {
              url: 'media',
              request: uploadedFile
            }
            var test = new FormData()
            test.append('file', this.$refs.workshop_picture.files[0])
            this.imagePost = test
            return data.request
          } else {
            this.errors.add('image', 'L\'image doit être au format png/gif/jpg/jpeg')
            return
          }
        },
        showImagePreview (picture) {
          var reader = new FileReader()
          reader.onload = (e) => {
            this.$refs.image_preview.src = e.target.result
          }
          reader.readAsDataURL(picture)
        },
        part2: function () {
          this.scrollTop()
          if (this.MainType === 'community') {
            this.$wraper.style.width = '300vw'
            this.isCommunity = true
            TweenMax.to('.wraper', 0.5, {left: '-100%'})
          } else if (this.MainType === 'archives') {
            this.isArchives = true
            TweenMax.to('.wraper', 0.5, {left: '-100%'})
          }
        },
        goToForm: function () {
          this.scrollTop()
          TweenMax.to('.wraper', 0.5, {left: '-200%'})
        },
        organizeCategories: function () {
          for (var i = 0; i < this.categories.length; i++) {
            if (this.categories[i].description === 'Archives') {
              this.categoryArchives.push(this.categories[i])
            }
          }
        },
        scrollTop () {
          if (navigator.userAgent.match(/firefox/i) || navigator.userAgent.match(/windows nt/i)) {
            TweenMax.to(document.getElementsByTagName('html')[0], 0.2, {scrollTop: 0, delay: 0.5})
          } else {
            TweenMax.to(document.body, 0.2, {scrollTop: 0, delay: 0.5})
          }
        }
      }
    }
</script>
<style src="../styles/contribute.scss" lang="scss" ></style>



