<template>
    <div class="relatedComponent" :class='{shown : isShown}'>
        <div class="button"  @click='related'>
          <div class="text">Related</div>
          <ul class="icone open">
            <li class="first"></li>
            <li class="second"></li>
          </ul>
        </div>
        <div class="relatedComponent_container">
          <router-link :to="{ path: item.slug }"  class="relatedComponent_container__item" v-for='item in relatedItems'>
              <img :src="item.fi_medium" class="postImage" @click='noRelated'>
              <div class="titles">
                <h2 @click='noRelated'>{{item.title.rendered}}</h2>
                <div class="categories" v-for="category in item.cats" @click='noRelated'>
                  {{ category.name }}
                </div>
              </div>
            </router-link>
        </div>

    </div>
</template>

<script>
    import config from '../config'
    import TweenMax from 'gsap'

    export default{
      data () {
        return {
          msg: 'hello vue',
          promise: [],
          nicePromise: '',
          relatedItems: '',
          isShown: false
        }
      },
      props: ['items'],
      mounted: function () {
        TweenMax.from('.relatedComponent .button', 0.5, {opacity: 0, top: 100, delay: 1})
        for (var i = 0; i < this.items.length; i++) {
          this.promise.push(this.items[i].ID)
          this.nicePromise = this.promise.join(',')
        }
        // this.fetch()
      },
      updated: function () {
      },
      methods: {
        fetch: function () {
          this.$http.get(`${config.dataUrl}/posts/?include=${this.nicePromise}`).then(response => {
           // get body data
            this.relatedItems = response.body
            // TweenMax.from('.relatedComponent_container__item', 0.5, {opacity: 0, y: 100, delay: 0.5})
          }, response => {
            // error callback
          })
        },
        related: function () {
          if (this.isShown === true) {
            this.isShown = false
          } else {
            this.isShown = true
            this.fetch()
          }
        },
        noRelated: function () {
          this.isShown = false
        }
      }
    }

</script>
<style scoped src="../styles/related.scss" lang="scss" ></style>



