<template>
  <div class="home">
    <Header></Header>
        <div class="postList" v-infinite-scroll="loadMore"  infinite-scroll-disabled="busy" infinite-scroll-distance="10">
          <article v-for="post in filteredList" class="post">
            <router-link :to="{ path: post.slug }"  >
              <img :src="post.fi_medium" class="postImage">
            </router-link>
          </article>
        </div>
      <transition name="singleComponent" mode="out-in">
        <SingleComponent></SingleComponent>
      </transition>

    <div class="bottomBar">
        <router-link class='bottomLinks contributeLink' to='/contribute'><p>Contribute</p></router-link>
        <div class="bottomLinks filters" v-on:click="filtering"><p>Filter</p></div>
    </div>



    <!--FILTER-->
    <div class="filter by-category clearfix" v-if='isFiltering' :class='{shown: isFiltering}'>
      <div class="close" v-on:click="filtering">CLOSE</div>
      <div class="categories">
        <!--<label class="categories" v-for="category in categories" v-if="category.name != 'Uncategorized'">-->
        <div class="categoriesMain archives" v-for="category in categories" v-if="category.name == 'Archives' ">
          <input  @click="selectAll" :id="category.id" type="checkbox" :name="category.id" :value="category.id" v-model="categoryFilter"><label :for="category.id">{{ category.name }}</label>
          <div class="categoriesSecond">
            <div class="categoriesSecond__item" v-for="category in categoryArchives">
              <input class="inputArchives" :id="category.id" type="checkbox" :name="category.id" :value="category.id" v-model="categoryFilter"><label :for="category.id">{{ category.name }}</label>
            </div>
          </div>
        </div>
        <div class="categoriesMain community" v-for="category in categories" v-if="category.name == 'Community' ">
          <input  :id="category.id" type="checkbox" :name="category.id" :value="category.id" v-model="categoryFilter"><label :for="category.id">{{ category.name }}</label>
          <div class="categoriesSecond">
            <div class="categoriesSecond__item" v-for="category in categoryCommunity">
              <input  class="categoriesArchives" :id="category.id" type="checkbox" :name="category.id" :value="category.id" v-model="categoryFilter"><label :for="category.id">{{ category.name }}</label>
            </div>
          </div>

        </div>
      </div>
      <div class="bottom">
        <div>
          selected filters : ({{ categoryFilter.length }})
        </div>
      </div>
    </div>
    </div>

</template>

<script>
    // import UniquePost from '../store/uniquePost'
    // import TweenMax from 'gsap'
    import SingleComponent from './SingleComponent.vue'
    import Header from './Header.vue'
    import config from '../config'
    import TweenMax from 'gsap'
    export default{
      components: {
        SingleComponent,
        Header
      },
      data () {
        return {
          posts: [],
          nameFilter: '',
          categoryFilter: [],
          categories: '',
          filteredList: [],
          categoryArchives: [],
          categoryCommunity: [],
          isFiltering: false,
          page: 1

        }
      },
      beforeMount: function () {
         // GET /someUrl
        this.$http.get(`${config.dataUrl}/posts?per_page=20`).then(response => {
          // get body data
          this.posts = response.body
          this.feedPost()
        }, response => {
          console.log(response)
          // error callback
        })

        this.$http.get(`${config.dataUrl}/categories?per_page=20`).then(response => {
          // get body data
          this.categories = response.body
          this.organizeCategories()
          this.beforeRandom()
        }, response => {
          console.log(response)
          // error callback
        })
      },
      mounted: function () {

      },
      computed: {
        selectAll: {
          get: function () {
            return this.categoryArchives ? this.categoryFilter.length === this.categoryArchives.length : false
          },
          set: function (value) {
            var selected = []
            if (value) {
              this.categoryArchives.forEach(function (categoryArchive) {
                this.categoryFilter.push(this.category.id)
              })
            }
            this.selected = selected
          }
        }
      },
      updated: function () {
        this.$post = document.querySelectorAll('.post')
        this.$postImage = document.querySelectorAll('.postImage')
        // this.randomPlacement()
      },
      methods: {
        feedPost: function () {
          for (var i = 0; i < this.posts.length; i++) {
            this.filteredList.push(this.posts[i])
          }
          TweenMax.to('.post img', 0.5, {opacity: 1})
        },
        filter: function () {
          var _this = this
          if (this.categoryFilter.length === 0) {
            this.feedPost()
          } else {
            this.filteredList = []
            for (var i = 0; i < _this.posts.length; i++) {
              for (var j = 0; j < _this.categoryFilter.length; j++) {
                for (var k = 0; k < _this.posts[i].categories.length; k++) {
                  if (_this.posts[i].categories[k] === parseInt(_this.categoryFilter[j])) {
                    this.filteredList.push(_this.posts[i])
                  }
                }
              }
            }
          }
        },
        beforeRandom: function () {
          var that = this
          setTimeout(function () {
            that.randomPlacement()
            TweenMax.to('.post img', 0.5, {opacity: 1})
          }, 1000)
        },
        randomPlacement: function () {
          var containerWidth = this.$post[0].offsetWidth
          var containerHeight = this.$post[0].no
          for (var i = 0; i < this.$post.length; i++) {
            let imageWidth = this.$postImage[i].offsetWidth
            let imageHeight = this.$postImage[i].offsetHeight
            let randPosX = Math.floor((Math.random() * (containerWidth - (imageWidth * 1.2))))
            let randPosY = Math.floor((Math.random() * (containerHeight - (imageHeight * 1.2))))

            this.$postImage[i].style.left = randPosX + 'px'
            this.$postImage[i].style.top = randPosY + 'px'
          }
        },
        organizeCategories: function () {
          for (var i = 0; i < this.categories.length; i++) {
            if (this.categories[i].description === 'Archives') {
              this.categoryArchives.push(this.categories[i])
            } else if (this.categories[i].description === 'Community') {
              this.categoryCommunity.push(this.categories[i])
            }
          }
        },
        filtering: function () {
          if (this.isFiltering === true) {
            this.isFiltering = false
            this.filter()
          } else {
            this.isFiltering = true
            // TweenMax.staggerFrom(this.links, 0.3, {opacity: 0, y: 100, delay: 0.5}, 0.3)
          }
        },
        loadMore: function () {
          if (!this.$route.params.slug) {
            this.$http.get(`${config.dataUrl}/posts?per_page=20&offset=${this.page * 20}`).then(response => {
            // get body data
              this.posts.push(response.body)
              for (var i = 0; i < response.body.length; i++) {
                this.posts.push(response.body[i])
              }
              console.log(response.body)
              this.feedPost()
              this.randomPlacement()
            }, response => {
              console.log(response)
            // error callback
            })
            this.page = this.page + 1
          }
        }
      }
    }
</script>
<style src="../styles/home.scss" lang="scss" ></style>



