<template>
  <div class="header" :class='{menu: isBurgerMenu}'>
    <ul class="burgerMenu"  v-on:click="this.burgerMenu" >
      <li class="first"></li>
      <li class="second"></li>
      <li class="third"></li>
    </ul>
    <div class="title">
      <h1>PopPills</h1>
      <h2>Archive & community</h2>
    </div>
    <div class="menuContent">
      <div class="menuLinks">
        <router-link class='links' to="/">Home</router-link>
        <router-link class='links' to="/about">About</router-link>
        <router-link class='links' to="/contact">Contact</router-link>
      </div>
      <div class="sharing">
        <h3>Share</h3>
          <social-sharing url="http://poppills.org" inline-template>
          <div class="socialLinks">
              <network network="facebook">
                <i class="fa fa-facebook"><div class="facebook"></div></i>
              </network>
              <network network="twitter">
                <i class="fa fa-twitter"><div class="twitter"></div></i>
              </network>
          </div>
        </social-sharing>
      </div>
    </div>
  </div>
</template>

<script>
    import TweenMax from 'gsap'
    import SocialSharing from 'vue-social-sharing'
    export default{
      components: {
        SocialSharing
      },
      data () {
        return {
          msg: 'hello vue',
          isBurgerMenu: false
        }
      },
      mounted: function () {
        this.links = document.querySelectorAll('.links')
        console.log(this.links)
      },
      methods: {
        burgerMenu: function () {
          if (this.isBurgerMenu === true) {
            this.isBurgerMenu = false
          } else {
            this.isBurgerMenu = true
            TweenMax.staggerFrom(this.links, 0.3, {opacity: 0, y: 100, delay: 0.5}, 0.3)
          }
        }
      }
    }
</script>
<style src="../styles/header.scss" lang="scss" ></style>



