<template>

    <div v-if='isShown' class="singleComponent">
        <div class="header">
          <router-link class='close' to="/"><img src='../assets/group.png'></router-link>
        </div>
        <transition name="navigation" mode="out-in">
          <article  v-for="post in singlePost" :key='currentSlug' >
            <transition name="singleComponent" mode="out-in">
              <div class="title" >
                <h2 >{{ post.title.rendered }}</h2>
                <div class="categories" v-for="category in post.cats">
                  {{ category.name }}
                </div>
                <div class="progressBar">
                  <div class="progressBar_moving"></div>
                </div>

                <div class="social" :class="completeUrl">
                  <social-sharing :url="completeUrl" inline-template>
                    <div class="socialLinks">
                      <network network="facebook">
                        <i class="fa fa-facebook"><div class="facebook"></div></i>
                      </network>
                      <network network="twitter">
                        <i class="fa fa-twitter"><div class="twitter"></div></i>
                      </network>
                    </div>
                  </social-sharing>
                </div>
              </div>
            </transition>
            <div class="postContent">
              <img :src="image">
              <div class="postText" v-html="post.content.rendered ">
                {{ post.content.rendered }}
              </div>
            </div>
          </article>
        </transition>
      <Related :items="relatedLinks" :isShown='related'></Related>
      <div class="bottom">
        <router-link class='bottomLinks contributeLink' to='/contribute'><p>Contribute</p></router-link>
        <router-link class='bottomLinks next' :to="{ path: nextSlug }" >
          <p @click="next">next</p>
        </router-link>
      </div>
    </div>
</template>

<script>
    // import JSON
    import config from '../config'
    import Related from './Related.vue'
    import TweenMax from 'gsap'
    import SocialSharing from 'vue-social-sharing'
    export default{
      components: {
        Related,
        SocialSharing
      },
      data () {
        return {
          ok: false,
          singlePost: [],
          currentSlug: this.$route.params.slug,
          nextSlug: '',
          // slug: this.$route.params.slug,
          // currentSlug: this.$route.params.slug,
          isShown: false,
          imageId: '',
          image: '',
          relatedLinks: [],
          related: false,
          completeUrl: 'http://poppills.org' + this.$route.path
        }
      },
      created: function () {
        document.addEventListener('touchmove', this.pogressBar)
        window.addEventListener('mousewheel', this.pogressBar)
      },
      beforeUpdate: function () {
        document.removeEventListener('touchmove', this.pogressBar)
        window.removeEventListener('mousewheel', this.pogressBar)
      },
      updated: function () {
      },
      mounted: function () {
        if (this.currentSlug) {
          this.isShown = true
          this.$http.get(`${config.dataUrl}/posts`, {params: { slug: this.currentSlug }}).then(response => {
         // get body data
            this.singlePost = response.body
            this.imageId = response.body[0].featured_media
            this.relatedLinks = response.body[0].acf.related_content
            this.getImage()
            this.$http.get(`${config.dataUrl}/posts?before=${this.singlePost[0].date_gmt}&per_page=1`).then(response => {
           // get body data
              this.nextSlug = response.body[0].slug
            }, response => {
              console.log(response)
              // error callback
            })
          }, response => {
            console.log(response)
            // error callback
          })
        }
      },
      watch: {
        '$route' (to, from) {
          this.changePost(to.params.slug)
        }
      },
      methods: {
        changePost: function (e) {
          if (e) {
            this.currentSlug = e
            this.$http.get(`${config.dataUrl}/posts`, {params: { slug: e }}).then(response => {
           // get body data
              this.singlePost = response.body
              this.imageId = response.body[0].featured_media
              this.isShown = true
              this.relatedLinks = response.body[0].acf.related_content
              this.getImage()
              this.$http.get(`${config.dataUrl}/posts?before=${this.singlePost[0].date_gmt}&per_page=1`).then(response => {
             // get body data
                this.nextSlug = response.body[0].slug
              }, response => {
                console.log(response)
                // error callback
              })
            }, response => {
              console.log(response)
              // error callback
            })
          } else {
            this.isShown = false
          }
        },
        getImage: function () {
          var url = `${config.dataUrl}/media/` + this.imageId
          this.$http.get(url).then(response => {
           // get body data
            this.image = response.body.source_url
          }, response => {
            console.log(response)
              // error callback
          })
        },
        next: function () {
          this.scrollTop()
        },
        scrollTop () {
          if (navigator.userAgent.match(/firefox/i) || navigator.userAgent.match(/windows nt/i)) {
            TweenMax.to(document.getElementsByTagName('html')[0], 0.2, {scrollTop: 0, delay: 0.5})
          } else {
            TweenMax.to(document.body, 0.2, {scrollTop: 0, delay: 0.5})
          }
        },
        progressBar: function () {
          var pageWidth = this.postHeight.offsetHeight
          console.log('pageWidth', pageWidth)
        }
      }
    }
</script>
<style src="../styles/singleComponent.scss" lang="scss" ></style>



